import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-mysinglepost',
  templateUrl: './mysinglepost.component.html',
  styleUrls: ['./mysinglepost.component.css']
})
export class MysinglepostComponent implements OnInit {

  mysingleobject = {};
  myid = 0;

  // DEPENDENCY INJECTION
  constructor(private myroute: ActivatedRoute) { 
      this.myroute.params.subscribe(res => this.myid = res.id);

    fetch('https://jsonplaceholder.typicode.com/posts/' + this.myid )
      .then(res => res.json())
      .then(data => this.mysingleobject = data);
  }



  ngOnInit() {
  }

}
