import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';


@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {
  @Input() public homevar;
  @Output() public homedata = new EventEmitter();

  constructor() {      
  }

  ngOnInit() {
    // we are releasing the data from here
    this.homedata.emit("Hello Home I am sending data to you");
  }

}
